:root {
    --active-color: white; 
}

body, html{
    
    font-family:Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0;

    
}

@keyframes drawLine {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

.active {
    position: relative;
}

.active::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px; 
    background-color: var(--active-color); 
}
